/*
//------------------------------------------------------------------------------
// Multimedia
//------------------------------------------------------------------------------
*/

.lastUnit{
  display: block;
}

.caja-listado {
  position: relative;
  height: 302px;
  display: block;
  overflow: hidden;
  margin-bottom: 10px;
  &::before {
    content: '';
    background-color: color('black');
    opacity: .5;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 0;
  }
  //imagen
  .container-img,
  .container-video {
    height: 100%;
    figure {
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        max-height: 100%;
        z-index: -1;
      }
    }
  }
  // */ ----| contenedor |----*/
  .container-title-desc {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    .caja-titulo {
      font-family: $latoblack;
      @include font-size(l);
      text-transform: uppercase;
      color: color('white');
      text-align: center;
      margin: 20px 0 10px 0;
    }
    .caja-body {
      font-family: $crimson-textregular;
      @include type-layout(l, .96);
      font-size: 1rem;
      color: color('white');
      text-align: center;
      margin-bottom: 8px;
      .view-redes-sociales-item{
        position: relative;
        >.view-content{
          display: flex;
          justify-content: center;
          flex-wrap: wrap;
          >.views-row{
            padding: 0;
            width: 44px;
            margin: 0 10px 10px;
            .view-footer-redes-sociales{
              padding-left: 0;
              .view-content{
                .views-row{
                  width: 44px;
                  padding: 0;
                  .field-content{
                    .img-responsive{
                      width: 44px;
                      height: 44px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .caja-enlace {
      padding: 5px 0;
      display: none;
      @include respond-to(xl) {
        opacity: 0;
      }
      a {
        display: block;
        width: 100%;
        max-width: 180px;
        font-family: $latolight;
        font-size: 0.9rem;
        color: white;
        text-align: center;
        text-decoration: none;
        padding: 10px 20px;
        margin: 0 auto;
        border: 1px solid color('white');
        background-color: transparent;
        z-index: -1;
        &:hover {
          background: #fff;
          color: color('grey-dark');
        }
        @include respond-to(m) {
          background-color: color('black');
        }
        @include respond-to(xl) {
          bottom: 20px;
          position: absolute;
          left: 0;
          right: 0;
          transition: all ease .3s;
        }
      }
    }
  }
  @include respond-to(xl) {
    &:hover {
      .caja-enlace {
        opacity: 1;
        display: block;
        a {
          bottom: 10px;
          z-index: 2;
        }
      }
      &::after {
        content: '';
        border: 1px solid #fff;
        width: 90%;
        height: 85%;
        position: absolute;
        top: 7px;
        right: 0;
        left: 0;
        margin: 0 auto;
        z-index: 1;
        pointer-events: none;
      }
    }
  }
}

@media screen and (min-width: 555px) and (max-width: 767px) {
  .views-column {
    width: 50%;
    float: left;
  }
}
